@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
    margin: 200px;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto';
}

.input__element {
    width: 100%;
    padding: 0 18px;
    height: 48px;
    background-color: #fff;
    border-radius: 4px;
    margin: 8px 0;
    font-size: 14px;
    font-weight: 400;
    box-shadow: 2px 2px 0 2px transparent;
    border: 1px solid #b8b8b8;
    overflow: hidden;
    text-overflow: ellipsis;
    box-sizing: border-box;
}

.input__container {
    position: relative;
}

.input__label {
    font-weight: 500;
    margin-bottom: 8px;
    font-size: 16px;
    color: #3d3d3d;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: left;
}

.input__group {
    margin-bottom: 8px;
}

.toggler {
    height: 46px;
    background-color: #f5f5f5;
    border-left: 1px solid #b8b8b8;
    border-radius: 0 4px 4px 0;
    width: 90px;
    text-align: center;
    cursor: pointer;
    position: absolute;
    right: 1px;
    top: 9px;
}

.toggler:hover {
    background-color: #e0e0e0;
}

.toggler__show {
    display: block;
}

.toggler__hide {
    display: none;
}

.container {
    width: 300px;
}

.tooltip__header {
    margin: 6px 0;
}
.tooltip__list {
    margin: 5px 0 5px 0;
    padding: 0;
    list-style-type: none;
    text-align: left;
    line-height: 1.5em;
}

.tooltip__list li > svg {
    margin-right: 10px;
}

.strength__meter {
    display: flex;
    flex-direction: row;
    margin: 7px;
    align-items: center;
}

.progress__container {
    flex: 3 1;
    background-color: #191919;
}

.strength__string {
    flex: 1 1;
    margin: 0;
    font-size: 14px;
    text-transform: capitalize;
}

.weak {
    color: #f44336;
}

.average {
    color: #d35400;
}

.fair {
    color: #8bc34a;
}

.good {
    color: #27ae60;
}

.excellent {
    color: #009432;
}

button {
    flex: 0 0 auto;
    font-family: 'Roboto';
    color: #ffffff;
    background-color: #2c4bff;
    padding-right: 16px;
    padding-left: 16px;
    margin: 8px 0px;
    height: 48px;
    font-size: 18px;
    line-height: 1.5;
    font-weight: 500;
    border-radius: 4px;
    cursor: pointer;
    width: 100%;
    min-width: 0;
    flex: 0 0 auto;
    border: 0;
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto';
}

.App {
    margin-top: 50px;
}

.app__container {
    width: 480px;
    margin: auto;
    padding: 30px 60px;
    box-shadow: 0 7px 26px -2px rgba(0, 0, 0, 0.1);
    border: 1px solid #cfd6dc;
    border-radius: 4px;
}

.form__header {
    font-style: normal;
    font-weight: bold;
    line-height: 44px;
    font-size: 32px;
    margin-bottom: 8px;
    color: #3d3d3d;
    margin-bottom: 30px;
}

.logo__container {
    text-align: center;
}

